module.exports = (sequelize, DataTypes) => {
  const StreetName = sequelize.define("street_name", {
    Id: {
      type: DataTypes.UUID,
      defaultValue: DataTypes.UUIDV4,
      allowNull: false,
      primaryKey: true,
      field: "id"
    },
    name: { type: DataTypes.STRING }
  });

  return StreetName;
};
